.CheckoutBilling-Checkbox {
  display: inline-block;
  margin-bottom: 2.4rem; }
  @media (max-width: 810px) {
    .CheckoutBilling-Checkbox {
      margin-bottom: 2.8rem; } }
  .CheckoutBilling-Checkbox + .CheckoutAddressBook {
    margin-top: 0; }

.CheckoutBilling-TermsAndConditions {
  font-weight: 700;
  font-size: 1.26rem;
  margin: 0;
  margin-bottom: 2.4rem;
  display: flex;
  align-items: center; }
  @media (max-width: 810px) {
    .CheckoutBilling-TermsAndConditions {
      font-size: 1.47rem;
      margin-bottom: 2.8rem; } }
  .CheckoutBilling-TermsAndConditions-Checkbox {
    margin: 0;
    margin-left: 1.2rem; }
    @media (max-width: 810px) {
      .CheckoutBilling-TermsAndConditions-Checkbox {
        margin-left: 1.4rem; } }

.CheckoutBilling-TACLabel {
  display: inline; }

.CheckoutBilling-TACLink {
  color: var(--link-color);
  cursor: pointer; }
  .CheckoutBilling-TACLink:hover {
    text-decoration: underline; }
  .CheckoutBilling-TACLink:focus {
    text-decoration: underline; }
