.CheckoutAddressBook {
  margin: 2.4rem 0;
  width: 100%; }
  @media (max-width: 810px) {
    .CheckoutAddressBook {
      margin: 2.8rem 0; } }
  .CheckoutAddressBook-Wrapper {
    display: flex;
    flex-wrap: wrap; }
  .CheckoutAddressBook-CustomAddressWrapper {
    margin: 2.4rem 0; }
    @media (max-width: 810px) {
      .CheckoutAddressBook-CustomAddressWrapper {
        margin: 1.4rem 0; } }
  .CheckoutAddressBook .CheckoutAddressBook-Button {
    display: block; }
    @media (max-width: 810px) {
      .CheckoutAddressBook .CheckoutAddressBook-Button {
        width: 100%; } }
    .CheckoutAddressBook .CheckoutAddressBook-Button_isCustomAddressExpanded {
      display: none; }
