:root {
  --checkout-max-width: 1100px; }

@media (min-width: 811px) {
  .Checkout {
    margin-top: var(--header-total-height); } }

@media (max-width: 810px) {
  .Checkout {
    padding-bottom: calc(var(--navigation-tabs-height) + 100px); } }

@media (min-width: 811px) {
  .Checkout::before {
    content: '';
    background: var(--breadcrumbs-background);
    min-height: var(--breadcrumbs-height);
    display: block; } }

@media (max-width: 810px) {
  .Checkout-StickyButtonWrapper {
    border-top: 1px solid var(--primary-divider-color);
    background-color: #fff;
    position: fixed;
    padding: 1.2rem 1.2rem calc(env(safe-area-inset-bottom) + 1.2rem);
    bottom: 0;
    left: 0;
    width: 100%;
    z-index: 85; } }

@media (max-width: 810px) {
  .Checkout-StickyButtonWrapper {
    padding: 1.4rem 1.4rem calc(env(safe-area-inset-bottom) + 1.4rem); } }

@media (max-width: 810px) {
  .Checkout-StickyButtonWrapper .Button {
    width: 100%; } }

.Checkout-Step {
  max-width: 470px; }

.Checkout-Wrapper {
  padding-top: 1px;
  max-width: var(--checkout-max-width); }
  @media (min-width: 811px) {
    .Checkout-Wrapper {
      display: grid;
      grid-template-columns: repeat(2, minmax(calc(50% - 1.2rem), 470px));
      grid-gap: 2.4rem; } }

.Checkout-Heading {
  text-transform: none;
  font-weight: 400;
  font-size: 2rem;
  margin: 0 0 1.2rem; }
  @media (max-width: 810px) {
    .Checkout-Heading {
      margin: 0 0 1.4rem; } }

.Checkout-Title {
  text-transform: capitalize;
  font-weight: 600;
  font-size: 2.5rem;
  margin: 2.4rem 0; }
  @media (max-width: 810px) {
    .Checkout-Title {
      margin: 2.8rem 0;
      display: none; } }

@media (min-width: 811px) {
  .Checkout .Checkout-Discount {
    margin-top: 30px; } }

.Checkout .Checkout-ExpandableContentContent {
  margin-top: 0; }
