@media (max-width: 810px) {
  .CheckoutOrderSummary {
    display: none; } }

.CheckoutOrderSummary-Header {
  display: flex;
  justify-content: space-between;
  align-items: center;
  margin-bottom: 0; }

.CheckoutOrderSummary-ItemsInCart {
  font-size: 1.2rem;
  opacity: .5;
  text-transform: none;
  top: 2px;
  font-weight: 300; }

.CheckoutOrderSummary-SummaryItem {
  justify-content: space-between;
  display: flex;
  padding: 1.2rem 0;
  margin-bottom: 0; }
  @media (max-width: 810px) {
    .CheckoutOrderSummary-SummaryItem {
      padding: 1.4rem 0; } }
  .CheckoutOrderSummary-SummaryItem:last-child {
    margin-bottom: 0; }
  .CheckoutOrderSummary-SummaryItem::before {
    content: none; }

.CheckoutOrderSummary-OrderItems h3 {
  text-align: left; }

.CheckoutOrderSummary-CartItemList {
  list-style: none;
  margin: 0;
  padding: 0; }

.CheckoutOrderSummary-CartItemDescription {
  margin-top: 5px; }
  .CheckoutOrderSummary-CartItemDescription p {
    font-size: 1.32rem;
    line-height: 1.5; }
    @media (max-width: 810px) {
      .CheckoutOrderSummary-CartItemDescription p {
        font-size: 1.54rem; } }

.CheckoutOrderSummary-CartItem {
  display: grid;
  grid-template-columns: 9.6rem auto auto;
  grid-column-gap: .6rem;
  margin: 0 0 .6rem;
  padding: 0; }
  @media (max-width: 810px) {
    .CheckoutOrderSummary-CartItem {
      grid-template-columns: 11.2rem auto auto;
      grid-column-gap: .7rem;
      margin: 0 0 .7rem; } }
  .CheckoutOrderSummary-CartItem::before {
    content: none; }
  .CheckoutOrderSummary-CartItem > * {
    display: flex;
    flex-direction: column;
    justify-content: center; }

.CheckoutOrderSummary-Thumbnail {
  width: 9.6rem;
  height: 9.6rem; }
  @media (max-width: 810px) {
    .CheckoutOrderSummary-Thumbnail {
      width: 11.2rem;
      height: 11.2rem; } }

.CheckoutOrderSummary-Title p {
  margin: 0;
  padding: 0;
  text-align: left; }

.CheckoutOrderSummary-Details {
  justify-self: end; }

.CheckoutOrderSummary .ProductPrice {
  margin-top: 0; }

.CheckoutOrderSummary .CartItem-Wrapper {
  padding: 1.2rem 0; }
  @media (max-width: 810px) {
    .CheckoutOrderSummary .CartItem-Wrapper {
      padding: 1.4rem 0; } }
