@media (max-width: 810px) {
  .CheckoutSuccess {
    margin-top: 2.8rem;
    text-align: center; } }

@media (max-width: 810px) {
  .CheckoutSuccess-ContinueButton {
    width: 100%; } }

@media (min-width: 811px) {
  .CheckoutSuccess-ContinueButton {
    margin-top: 2.4rem; } }

@media (max-width: 810px) {
  .CheckoutSuccess-ButtonWrapper {
    padding: 1.4rem;
    position: fixed;
    left: 0;
    width: 100%;
    bottom: var(--navigation-tabs-height);
    border-top: 1px solid var(--primary-divider-color); } }

@media (max-width: 810px) {
  .CheckoutSuccess .Button {
    width: 100%;
    text-align: center; } }
