:root {
  --checkout-address-table-border: var(--secondary-base-color);
  --checkout-address-table-hover-border: var(--secondary-base-color);
  --checkout-address-table-check-mark-hover-color: var(--secondary-dark-color);
  --checkout-address-selected-color: var(--primary-base-color);
  --checkout-address-table-check-mark-selected-color: var(--checkout-address-table-border); }

.CheckoutAddressTable {
  width: 100%; }
  .CheckoutAddressTable:first-child .CheckoutAddressTable-Button {
    margin-top: 0; }
  .CheckoutAddressTable-Button {
    --check-mark-opacity: 0;
    border: 2px solid var(--checkout-address-table-border);
    margin: 1.2rem 1.2rem 0 0;
    transition-property: border-color, background, border-color;
    transition-duration: 150ms;
    transition-timing-function: ease-in;
    width: 100%; }
    @media (max-width: 810px) {
      .CheckoutAddressTable-Button {
        margin: 1.4rem 1.4rem 0 0; } }
    .CheckoutAddressTable-Button::before, .CheckoutAddressTable-Button::after {
      content: '';
      opacity: var(--check-mark-opacity);
      position: absolute;
      z-index: 1;
      transition: opacity 150ms ease-in; }
    .CheckoutAddressTable-Button::before {
      top: 6px;
      right: 6px;
      width: 24px;
      height: 24px;
      border-radius: 50%;
      background: var(--checkout-address-table-border); }
    .CheckoutAddressTable-Button::after {
      top: 12px;
      right: 11px;
      width: 12px;
      height: 6px;
      transform: rotate(135deg);
      border-right: 2px solid var(--check-mark-color);
      border-top: 2px solid var(--check-mark-color); }
    .CheckoutAddressTable-Button:not(.CheckoutAddressTable-Button_isSelected):hover {
      --check-mark-opacity: 1;
      --checkout-address-table-border: var(--checkout-address-table-hover-border);
      --check-mark-color: var(--checkout-address-table-check-mark-hover-color); }
    .CheckoutAddressTable-Button_isSelected {
      --check-mark-opacity: 1;
      --checkout-address-table-border: var(--checkout-address-selected-color);
      --check-mark-color: var(--checkout-address-table-check-mark-selected-color); }
  .CheckoutAddressTable-SelectedLabel {
    color: var(--checkout-address-selected-color);
    text-transform: uppercase; }
    .CheckoutAddressTable-SelectedLabel::before {
      content: ' - ';
      color: #0a0a0a; }
  .CheckoutAddressTable .KeyValueTable {
    margin: 0; }
  .CheckoutAddressTable td,
  .CheckoutAddressTable th {
    width: auto; }
